@import "../../../assets/scss/colorVariables";

.input-number{
    .input-group{
        background-color: $white;
        
        .input-group-text {
            border: 0;
            cursor: pointer;
            color: $white;
            border-radius: 4px !important;
            font-size: 24px;
            width: 45px;
            height: 45px;
            display: flex;
            justify-content: center;
            user-select: none;
            &.minus{
                background-color: $red;
            }
            &.plus{
                background-color: $teal;
            }
        }

        input.form-control {
            min-height: 45px;
            border: 0;
            border-radius: 0;
            outline: 0;
            box-shadow: 0;
            background-color: $gray-100;
            padding: 0.575rem 1rem;
            text-align: center;
        }
    }
}